.contact-container {
    padding: 40px;
    text-align: center;
}

.contact-title {
    font-size: 32px;
    color: #333;
    margin-bottom: 20px;
}

.contact-description {
    font-size: 18px;
    color: #666;
    margin-bottom: 30px;
}

.contact-form {
    margin-bottom: 30px;
}

.contact-button {
    font-size: 16px;
    color: #fff;
    background-color: #28a745;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.contact-button:hover {
    background-color: #218838;
}

.contact-section {
    margin-top: 50px;
}

.contact-section-title {
    font-size: 24px;
    color: #444;
    margin-bottom: 20px;
}

.contact-list {
    list-style: none;
    padding: 0;
    text-align: left;
}

.contact-list li {
    font-size: 18px;
    color: #555;
    margin-bottom: 10px;
}

.contact-link {
    color: #007bff;
    text-decoration: none;
    transition: color 0.3s ease;
}

.contact-link:hover {
    color: #0056b3;
}
